@import ../../../../../styles/helpers

.DaySelector
    height: 350px
    max-width: 300px
    width:200px

.DaySelector__list
    height: 100%
    overflow: auto

.DaySelector__list li
    padding: 0.5em 0.5em 0.5em
    cursor: pointer
    transition: all 0.5s ease-out
    border-radius: 12px
    &:hover, &.active
        background-color: $n5
    
